import React from "react";
import { Link } from "gatsby";
import Image from "../../../../components/Image";

export default function RelatedPosts(props) {
    const {relatedPosts} = props;

    return (
        <>
        {relatedPosts.edges.length > 1 &&
            <section className="RelatedPostsCtr">
                <h2>Related Posts</h2>
                <div className="RelatedPosts">
                    {relatedPosts.edges.map(({ node }) => {
                        const { cover_photo, slug, title } = node.frontmatter;
                        return (
                            <Link className="RelatedPostCard" to={`/${slug}`} key={slug} title={title}>
                                <article>
                                    <div className="RelatedPostImgCtr">
                                        <Image src={cover_photo} alt={title} />
                                    </div>
                                    <div className="RelatedPostTitleCtr">
                                        {title}
                                    </div>
                                </article>
                            </Link>
                        );
                    })}
                </div>
            </section>
        }
        </>
    );
}
  