import React from "react";
import Image from "../../../../../components/Image";
import BlogLink from "../../OutboundLink";
import "./styles.scss";

export default function LocationCard(props) {
    const {description, imgsrc, location_link, title} = props;

    return (
        <div className="LocationCard">
            <header>
                <Image src={imgsrc} alt={title} />
                <BlogLink href={location_link} title={title}>
                    <h5>{title}</h5>
                </BlogLink>
            </header>
            <p>{description}</p>
        </div>
    );
}
  