import React from "react";
import Link from "../../../../components/Link";
import { Link as InternalLink } from "gatsby";

import {domain, isTechWaifuUrl} from "../../../../url/urlChecker";

export default function BlogLink(props) {
    if (isTechWaifuUrl(props.href)) {
        return (<InternalLink to={props.href.substring(domain.length)}>{props.children}</InternalLink>);
    }
    return <Link {...props} />;
}