import React, { useEffect, useState } from "react";
import ReactGA from 'react-ga';

import { Parallax, Background } from 'react-parallax';

import Layout from "../../../components/layout";
import Author from "../../../components/Author";

// Blog Components
import Body from "../components/Body";

import "./styles.scss";

export default function TravelTemplate(props) {
    const { data } = props;
    const [start] = useState(new Date());
    const { blog } = data;
    const { fields, frontmatter, id } = blog;

    useEffect(() => {
        return () => {
            const cur = new Date();
            const diff = cur.getMilliseconds() - start.getMilliseconds();
            ReactGA.event({
                category: 'TimeOnBlog',
                variable: 'time',
                value: diff, // in milliseconds
                label: id,
            });
        };
    });

    return (
        <Layout>
            <div className="travel-blog-post-container">
                <header>
                    <Parallax
                        blur={5}
                        bgImage={frontmatter.cover_photo}
                        bgImageAlt={frontmatter.title}
                        strength={100}>
                        <div className="TravelBlogHeader">
                            <Background className="custom-bg">
                                <div className="blog-post-container">
                                    <section className="blog-post-content">
                                        <h1 className="Title">{frontmatter.title}</h1>
                                        <div className="Date">Published: {frontmatter.date}</div>
                                        <div className="AuthorBubbleCtr">
                                            <Author id={frontmatter.author} />
                                        </div>
                                        <div className="TimeToRead">{fields.readingTime.text}</div>
                                    </section>
                                </div>
                            </Background>
                        </div>
                    </Parallax>
                </header>

                <div className="blog-post-container">
                    <Body {...props} />
                </div>
            </div>
        </Layout>
    )
}