import React from "react";
import rehypeReact from "rehype-react";

// Blog Components
import BlogContainer from "../blog";
import TravelBlogContainer from "../travel";
import SEO from "../../../components/SEO";
import Image from "../../../components/Image";
import AmazonAd from "../../../components/AmazonAd";
import Youtube from './../components/Youtube';
import Review from './../components/Review';
import MediaAds from './../components/MediaAds';
import Caption from './../components/Caption';
import Table from './../components/Table';
import Paragraph from './../components/Paragraph';
import UnorderedList from './../components/UnorderedList';
import OrderedList from './../components/OrderedList';
import AmazonAffiliate from "./../components/AmazonAffiliate";
import ImageComparison from './../components/ImageComparison';
import AnalyticsLink from './../components/OutboundLink';
import ImageCarousel from "./../components/ImageCarousel";
import LocationCard from "./../components/Travel/LocationCard";
import {AUTHOR_MAP} from "../../../author/authorInfo";

import {createBlogPostingMetadata, createReviewMetadata} from "../../../jsonLd";

import "../styles.scss"; 

const renderAst = new rehypeReact({
  createElement: React.createElement,
  components: {
    "img": Image,
    "img-carousel": ImageCarousel,
    "youtube": Youtube,
    "img-compare": ImageComparison,
    "img-caption": Caption,
    "review": Review,
    "a": AnalyticsLink,
    "ul": UnorderedList,
    "ol": OrderedList,
    "amazon-affiliate": AmazonAffiliate,
    "amazon-ad": AmazonAd,
    "media-ads": MediaAds,
    "table": Table,
    "location-card": LocationCard,
    p: (props) => {
      const firstChild = props.children[0];
      if (typeof firstChild !== 'object') {
        return (
          <Paragraph>{props.children}</Paragraph>
        );
      }

      return props.children;
    },
  },
}).Compiler;

const spacer = {
  "type": "text",
  "value": "\n"
};

const ad_1 = {
  "type": "element",
  "tagName": "p",
  "properties": {},
  "children": [{
    "type": "element",
    "tagName": "media-ads",
    "properties": {
      "type": "blog-content"
    },
    "children": []
  }]
};

const ad_2 = {
  "type": "element",
  "tagName": "p",
  "properties": {},
  "children": [{
    "type": "element",
    "tagName": "media-ads",
    "properties": {
      "type": "blog-content-2"
    },
    "children": []
  }]
};

function addAdvertisementsToAst(root) {
  const children = root.children;

  const firstAdLocation = parseInt(children.length * .40);
  const secondAdLocation = parseInt(children.length * .85);
  let childrenWithAds = children.slice(0, firstAdLocation)
  .concat([spacer, ad_1, spacer])
  .concat(children.slice(firstAdLocation, secondAdLocation));

  if (children.length > 20) {
    childrenWithAds = childrenWithAds.concat([spacer, ad_2, spacer])
  }

  childrenWithAds = childrenWithAds.concat(children.slice(secondAdLocation))

  return {
    ...root,
    children: childrenWithAds,
  };
}

export default function BlogInMarkdown({
  data
}) {
  const { blog, site } = data;
  const { excerpt, frontmatter, html, htmlAst, wordCount } = blog;
  const slug = frontmatter.slug;

  const coverPhoto = frontmatter.cover_photo;

  let metadataList = [
    {
      ...createBlogPostingMetadata({
        authorId: frontmatter.author || "mai",
        mainEntityOfPage: "True",
        headline: frontmatter.title,
        coverPhoto: coverPhoto,
        datePublished: frontmatter.date,
        dateModified: frontmatter.modified_date,
      }),
      "genre": frontmatter.related_tag,
      "keywords": frontmatter.tags.join(", "),
      "wordcount": `"${wordCount.words}"`,
      "url": `${site.siteMetadata.siteUrl}/${slug}`,
      "description": `"${excerpt}"`,
      "articleBody": `"${html}"`,
      "inLanguage": "en-US",
    }
  ];
  if (frontmatter.is_review) {
    metadataList.push(createReviewMetadata({
      authorId: frontmatter.author || "mai",
      score: frontmatter.score,
      description: frontmatter.review_description,
      brandName: frontmatter.brand_name,
      datePublished: frontmatter.date,
      dateModified: frontmatter.modified_date,
      itemName: frontmatter.item_name,
      img: coverPhoto
    }));
  }

  const astWithAds = addAdvertisementsToAst(htmlAst);

  if (frontmatter.is_travel_blog) {
    return (
      <TravelBlogContainer data={data} >
        <>
          <SEO
            article={true}
            author={AUTHOR_MAP[frontmatter.author].name}
            title={frontmatter.title}
            image={coverPhoto}
            description={excerpt}
            keywords={frontmatter.tags}
            urlPath={`/${slug}`}
            metadata={metadataList}
          />
          {renderAst(astWithAds)}
        </>
      </TravelBlogContainer>
    );
  }

  return (
    <BlogContainer data={data} >
      <>
        <SEO
          article={true}
          author={AUTHOR_MAP[frontmatter.author].name}
          title={frontmatter.title}
          image={coverPhoto}
          description={excerpt}
          keywords={frontmatter.tags}
          urlPath={`/${slug}`}
          metadata={metadataList}
        />
        {renderAst(astWithAds)}
      </>
    </BlogContainer>
  );
};