import React, {useEffect} from "react";
import ReactGA from 'react-ga';

import "./styles.scss";

export default function Review(props) {
    useEffect(() => {
        ReactGA.event({
            category: 'Review',
            action: 'Appeared',
            value: props.id,
        });
    });

    const data = JSON.parse(props.data.split("'").join("\""));

    let ScoreColor = "Bad";
    if (data.score >= 90) {
        ScoreColor = "Great";
    } else if (data.score >= 80) {
        ScoreColor = "Good";
    } else if (data.score >= 70) {
        ScoreColor = "Ok";
    } 

    return (
        <div className="ReviewContainer">
            <div className="ScoreContainer">
                <div className={`ScoreBox ${ScoreColor}`}>
                    {data.score}
                </div>
            </div>

            <div className="ProsConsContainer">
                <div className="Pros">
                    <h4>Pros</h4>
                    <ul>
                        {data.pros.map((d, i) => {
                            return (<li key={i}>{d}</li>)
                        })}
                    </ul>
                </div>
                <div className="Cons">
                    <h4>Cons</h4>
                    <ul>
                        {data.cons.map((d, i) => {
                            return (<li key={i}>{d}</li>)
                        })}
                    </ul>
                </div>
            </div>
        </div>
    );
}