import React from "react";
import { Link } from "gatsby";

export default function Tags(props) {
    const {tags} = props;

    return (
        <section className="blog-post-tags">
            {tags.map((tag) => {
                const sanitizedTag = tag.toLowerCase().split(" ").join("_").trim();
                return (<Link key={tag} className="blogTag" to={`/blogs/${sanitizedTag}`}>#{tag}</Link>);
            })}
        </section>
    );
}
  