import { graphql } from "gatsby";

import BlogInMarkdown from "./view";

export default BlogInMarkdown;

export const pageQuery = graphql`
  query($slug: String!, $relatedTag: String!) {
    site {
      siteMetadata {
        siteUrl
      }
    }
    blog: markdownRemark(frontmatter: { slug: { eq: $slug } }) {
      html
      htmlAst
      id
      excerpt
      frontmatter {
        author
        date(formatString: "MMMM D, YYYY")
        modified_date(formatString: "MMMM D, YYYY")
        slug
        title
        tags
        cover_photo
        related_tag
        is_travel_blog
        has_affiliate_links
        is_review
        brand_name
        item_name
        review_description
        score
      }
      wordCount {
        words
      }
      fields {
        readingTime {
          text
        }
      }
    }
    relatedPosts: allMarkdownRemark(
      limit: 3,
      filter: {
        frontmatter:{tags:{in: [$relatedTag]}, slug:{ne:$slug} }
      }
    ) {
      edges {
        node {
          frontmatter {
            slug
            title
            cover_photo
          }
        }
      }
    }
  }
`
