import React from "react";

// Blog Components
import Tags from '../Tags';
import RelatedPosts from '../RelatedPosts';
import CommentsSection from "../CommentsSection";
import MediaAds from '../MediaAds';
import AffiliateInfo from "../AffiliateInfo";

export default function Body(props) {
    const { data } = props;
    const { relatedPosts, blog, } = data;
    const { frontmatter, id } = blog;

    return (
        <div>
            <section className="blog-post-content">
                {props.children}

                {frontmatter.has_affiliate_links &&
                    <AffiliateInfo />
                }
            </section>
            <Tags tags={frontmatter.tags} />
            <RelatedPosts relatedPosts={relatedPosts} />
            <MediaAds type="billboard" />
            <CommentsSection id={id} title={frontmatter.title} />
        </div>
    )
}